import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/router';
import { Box, CircularProgress, Dialog, IconButton, DialogTitle, DialogContent, LinearProgress, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import FindInPageIcon from '@mui/icons-material/FindInPage';
import { readFile, sendApiRequest } from 'lib/api/public';
import Custom404 from 'components/common/Custom404';
import FilesComboWithPreview from 'components/common/FilesComboWithPreview';

const Job = () => {
  const [job, setJob] = useState(null);
  const [isLoading, setLoading] = useState(false);
  const [notFound, setNotFound] = useState(false);
  const [candidates, setCandidates] = useState([]);
  const [selectedCandidate, setSelectedCandidate] = useState(null);

  const router = useRouter();
  const { observerLink } = router.query;

  const getJob = () => {
    if (!observerLink) {
      return;
    }

    setLoading(true);

    sendApiRequest({
      path: `/portal/observe/job?observerLink=${observerLink}`,
      method: 'GET',
    })
      .then((response: any) => {
        setLoading(false);

        if (!response.error) {
          setJob(response.job);
          setCandidates(response.candidates);
        }
      })
      .catch((e) => {
        if (e.message.includes('Job not found')) {
          setNotFound(true);
        }

        setLoading(false);
        setJob(null);
      });
  };

  useEffect(() => {
    getJob();
  }, [observerLink]);

  if (notFound) {
    return <Custom404 />;
  }

  if (!job) {
    return null;
  }

  const es = job.estimatedSalary || {};
  const minSalary = es.minValue;
  const maxSalary = es.maxValue;

  const formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  });

  const renderCandidateDetails = () => {
    if (!selectedCandidate) {
      return null;
    }

    const onClose = () => {
      setSelectedCandidate(null);
    }

    const candidate = selectedCandidate;

    return (
      <Dialog
        open={true}
        fullScreen
        maxWidth="lg"
        style={{ width: '50%', left: 'inherit' }}
        onClose={onClose}
        slotProps={{
          backdrop: {
            sx: { display: 'none' },
          },
        }}
        PaperProps={{
          sx: {
            pointerEvents: 'auto',
          },
        }}
      >
        <DialogTitle sx={{ display: 'flex', alignItems: 'center' }}>
          {candidate.avatarUrl ? <img style={{ borderRadius: '50px', height: '30px', marginRight: '10px' }} src={readFile(candidate.avatarUrl)} /> : null}
          <span>
            {candidate.firstName} {candidate.lastName}
          </span>

          <IconButton onClick={onClose} sx={{ marginLeft: 'auto' }}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>

        <DialogContent>
          {isLoading ? <CircularProgress /> : null}
          <Box>
            <FilesComboWithPreview key={Math.random()} files={candidate.resumes} />
          </Box>
        </DialogContent>
      </Dialog >
    )
  }

  return (
    <div id="observe-job">
      {renderCandidateDetails()}
      {isLoading ? <LinearProgress /> : null}

      <div id="observer-job-detail">
        <h1 className="title">{job.title}</h1>

        <div>
          <strong>{job.location}</strong>
        </div>

        <div>
          <strong>{job.locationType}</strong>
        </div>

        <div dangerouslySetInnerHTML={{ __html: job.description }} />

        <div>
          {minSalary && maxSalary ? (
            <div>
              {formatter.format(minSalary)} - {formatter.format(maxSalary)} USD/year
            </div>
          ) : null}
        </div>
      </div>

      <div id="observer-job-candidates">
        <h2>Candidates</h2>

        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} size="small">
            <TableHead>
              <TableRow>
                <TableCell>#</TableCell>
                <TableCell></TableCell>
                <TableCell>Name</TableCell>
                <TableCell>Location</TableCell>
                <TableCell>Emails</TableCell>
                <TableCell>Phones</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {candidates.map((candiate, index) => (
                <TableRow
                  key={candiate._id}
                >
                  <TableCell>{index + 1}</TableCell>
                  <TableCell>
                    <FindInPageIcon
                      color="action"
                      sx={{ cursor: 'pointer' }}
                      onClick={() => { setSelectedCandidate(candiate); }}
                    />
                  </TableCell>
                  <TableCell>
                    {candiate.firstName} {candiate.lastName}
                  </TableCell>
                  <TableCell>{candiate.city && candiate.state ? `${candiate.city}, ${candiate.state}` : null}</TableCell>
                  <TableCell>{candiate.emails.map((email) => email.email).join(', ')}</TableCell>
                  <TableCell>{candiate.phones.map((phone) => phone.phone).join(', ')}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>

      </div>
    </div>
  );
};

export default Job;
