import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/router';
import { readFile } from '../../lib/api/public';
import { Select, MenuItem } from '@mui/material';

type File = {
    name: string;
    url: string;
    date: Date;
}

type Props = {
    files: File[];
    rightSide?: React.ReactNode;
};

const fcSx = {
    '& .MuiSelect-select': {
        width: '200px',
        padding: '10px', // Adjust padding as needed
    },
}

const Files = ({ files, rightSide }: Props) => {
    const router = useRouter();
    const { search } = router.query;

    const [selectedAttachmentUrl, setSelectedAttachmentUrl] = useState((files || []).find(file => file.url.includes('pdf'))?.url || '');

    useEffect(() => {
        setTimeout(() => {
            const element: any = document.getElementById('pdf-js-viewer');

            if (element) {
                const iframeDocument = element.contentWindow;

                iframeDocument.PDFViewerApplication.eventBus.dispatch('find', {
                    caseSensitive: false,
                    findPrevious: undefined,
                    highlightAll: true,
                    phraseSearch: true,
                    query: search
                });
            }

        }, 3000)
    }, [])

    const renderPreview = () => {
        if (!selectedAttachmentUrl) {
            return null;
        }

        let url = readFile(selectedAttachmentUrl);

        if (url.includes('pdf')) {
            url = `/pdfjs/web/viewer.html?file=${url}&search=Scientist&phrase=true`;
        }

        return (
            <iframe
                src={url}
                id="pdf-js-viewer"
                width="100%"
                height="100%"
                style={{ border: 'none', outline: 'none' }}
            ></iframe>
        )
    }

    return (
        <div style={{ height: '100vh' }}>
            <Select value={selectedAttachmentUrl} sx={fcSx} onChange={(e) => {
                setSelectedAttachmentUrl(e.target.value as string);
            }}>
                {(files || []).map((attachment, index) => {
                    return (
                        <MenuItem value={attachment.url} key={index}>{attachment.name}</MenuItem>
                    )
                })}
            </Select>

            {'  '} {rightSide}

            <br />
            <br />

            {renderPreview()}
        </div>
    )
};

export default Files;